@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --font-primary: 'Poppins', sans-serif;

  --colorT-lilac: rgba(176,172,251, 1);
  --colorT-lila: rgba(65,57,255, 1);
  --colorT-gris: rgba(135, 135, 148, .3);
  --colorT-rojo: rgba(245, 5, 16, 1);
  --colorT-bordo: rgba(115, 6, 11, 1);
  --colorT-celeste: rgb(71, 192, 223);

  
  --color-blanco: #ffffff;
  --color-negro: #000000;
  --color-celeste:#ddecff;
  --color-azul: #0A4580;
  --color-gris: #6c757d;

}

*{
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  font-family: var(--font-primary);
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
  
}

::selection {
  color: rgb(71, 192, 223);
  color: var(--colorT-celeste);
  background: rgba(135, 135, 148, .3);
  background: var(--colorT-gris);
}


.App{
  position: relative;
}

